/**
 * Grid 宫格
 * @description 宫格组件
 * @tutorial https://ext.dcloud.net.cn/plugin?id=27
 * @property {Number} column 每列显示个数
 * @property {String} borderColor 边框颜色
 * @property {Boolean} showBorder 是否显示边框
 * @property {Boolean} square 是否方形显示
 * @property {Boolean} Boolean 点击背景是否高亮
 * @event {Function} change 点击 grid 触发，e={detail:{index:0}}，index 为当前点击 gird 下标
 */
export default {
	name: 'UniGrid',
	props: {
		// 每列显示个数
		column: {
			type: Number,
			default: 3
		},
		// 是否显示边框
		showBorder: {
			type: Boolean,
			default: true
		},
		// 边框颜色
		borderColor: {
			type: String,
			default: '#e5e5e5'
		},
		// 是否正方形显示,默认为 true
		square: {
			type: Boolean,
			default: true
		},
		highlight: {
			type: Boolean,
			default: true
		}
	},
	provide() {
		return {
			grid: this
		}
	},
	data() {
		const elId = `Uni_${Math.ceil(Math.random() * 10e5).toString(36)}`
		return {
			elId,
			width: 0
		}
	},
	created() {
		this.children = []
	},
	mounted() {
		this.init()
	},
	methods: {
		init() {
			setTimeout(() => {
				this._getSize((width) => {
					this.children.forEach((item, index) => {
						item.width = width
					})
				})
			}, 50)
		},
		change(e) {
			this.$emit('change', e)
		},
		_getSize(fn) {

			uni.createSelectorQuery()
				.in(this)
				.select(`#${this.elId}`)
				.boundingClientRect()
				.exec(ret => {
					this.width = parseInt((ret[0].width-1) / this.column) + 'px'
					fn(this.width)
				})







		}
	}
}
